export default {
  name: 'Ebook',
  data() {
    return {
      settingOpen: false,
      catalogOpen: false,
      penUse: false,
    }
  },
  methods: {
    handleOpenSetting() {
      this.settingOpen = !this.settingOpen
      this.catalogOpen = false
      this.penUse = false
    },
    handleOpenCatalog() {
      this.catalogOpen = !this.catalogOpen
      this.settingOpen = false
      this.penUse = false
    },
    handleUsePen() {
      this.penUse = !this.penUse
      this.settingOpen = false
      this.catalogOpen = false
    },
  },
}
